import { HttpErrorResponse } from '@angular/common/http';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { even } from '@rxweb/reactive-form-validators';
import { Observable, of, Subject } from 'rxjs';
import { CanDeactivateGuard, IDeactivateComponent } from 'src/app/shared/guards/can-deactivate.guard';
import { permissionsLabels } from 'src/app/shared/models/fmosNewRolePermissions';
import { ApiService } from 'src/app/shared/services/api.service';
import { RoleService } from 'src/app/shared/services/role.service';
import { ToastService } from 'src/app/shared/services/toast.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'npr-process-code-mapping-edit',
  templateUrl: './process-code-mapping-edit.component.html',
  styleUrls: ['./process-code-mapping-edit.component.scss']
})
export class ProcessCodeMappingEditComponent implements OnInit ,IDeactivateComponent{
  roleCodes = new permissionsLabels();
  processCodeEditForm: FormGroup;
  processCodeSummary: any;
  extSysCodeData: any;
  showFilefield: boolean = false;
  showFutureDate: boolean = false;
  showfCYExchangeValue: boolean = false;
  showbackDateValue: boolean = false;
  showsftpValues: boolean = false;
  autoUploadValue: any;
  futuredataValue: any;
  fCYExchangeValue: any
  backDateValue: any;
  backdate: any;
  sftpValues: any;
  currentUser: string;
  editFlag: boolean = false;
  processCode:boolean = false;
  usererror:boolean = false;
  passworderror:boolean = false;
  adresserror:boolean = false;    
  patherror:boolean = false;
  porterror:boolean = false;
  futuredate:boolean = false;
  backdates:boolean = false;
  submitEdit: boolean;
  extName: any;
  formTouched: boolean=true;
  filepath:boolean=false
  fileName:boolean=false
  showDialog = false;
  subject = new Subject<boolean>();
  var: any;
  processCodeFlag:boolean=false;
  FCYpercent:Boolean = false;
  UsernameFlag: boolean = false;

  constructor(private router: Router, private apiService: ApiService,private roleService: RoleService, private formBuilder: FormBuilder,
     private toastService: ToastService,private cdr: ChangeDetectorRef) { }

  ngOnInit(): void {
    setTimeout(() => {
      this.newRolePermissions();
    }, 2000);
    this.roleService.screenLabelList.subscribe(message => this.roleCodes = message);
    setTimeout(() => {
      console.log(this.roleCodes);
    }, 3000);
    this.currentUser = localStorage.getItem("userFromLogin");
    this.processCodeEditForm = this.formBuilder.group({
      extSysCode: ["", [Validators.required]],
      extSysName: ["", [Validators.required]],
      processCode: ["", [Validators.required]],
      maxUploadLimit: ["", [Validators.required]],
      acft: [""],
      verifiedOnce:[""],
      recordStatus:[""],
      authStatus:[""],
      verifierDtStamp:[""],
      verifierId:[""],
      autoUpload: [""],
      backDateAllow: [""],
      bdav: [""],
      creatorId: [""],
      fCYPercentage: [""],
      fCYRateVariance: [""],
      fdav: [""],
      fileName: [""],
      filePath: [""],
      futureDateAllow: [""],
      mandatoryFieldImposed: [""],
      sftpIpaddress: [""],
      sftpPassword: [""],
      sftpPath: [""],
      sftpPort: [""],
      sftpRequired: [""],
      sftpUsername: [""],
      treasuryrefRequired: [""],
      wuFile: [""],
      id:[""],
      versionNo:[""]
    });
    this.gettingProcessCode();
    // this.auditLog();
  }
  newRolePermissions()
  {
    this.roleService.fetchScreenPermissions('Process Code Mapping');
  }

  onCheckboxChange() {
    this.fileName = false;
    this.filepath = false;
  }
  
  // keypressName(event) {
  //   if(event.target.value === ' ') {
  //     this.fileName = true;
  //   } else {
  //     this.fileName = false
  //   }
  //   event.target.value = event.target.value.replace(/^\s+/g, '')
  // }
  // keypressFilepath(event) {
  //   if(event.target.value === ' ') {
  //     this.filepath = true;
  //   } else {
  //     this.filepath = false
  //   }
  //   event.target.value = event.target.value.replace(/^\s+/g, '')
  // }
  auditLog() {
    if (this.processCodeSummary.authStatus =='U' || this.processCodeSummary.authStatus =='Unauthorized') {
      this.processCodeSummary.authStatus = 'UNAUTHORIZED'
    }
    if (this.processCodeSummary.authStatus == 'A' || this.processCodeSummary.authStatus =='Authorized' ) {
      this.processCodeSummary.authStatus = 'AUTHORIZED'
    }
    if (this.processCodeSummary.recordStatus == 'O' || this.processCodeSummary.recordStatus == 'Open') {
      this.processCodeSummary.recordStatus = 'OPEN'
    }
    if (this.processCodeSummary.recordStatus == 'C' || this.processCodeSummary.recordStatus == 'Close') {
      this.processCodeSummary.recordStatus = 'CLOSED'
    }
    if (this.processCodeSummary.verifiedOnce == 'N') {
      console.log("NO");
      this.processCodeSummary.verifiedOnce = 'NO'
    }
    if (this.processCodeSummary.verifiedOnce == 'Y') {
      this.processCodeSummary.verifiedOnce = 'YES'
    }
  }
  gettingProcessCode() {
    this.apiService.getProcessCode().subscribe((summaryData) => {
      this.processCodeSummary = summaryData;
      var retrievedObject = localStorage.getItem('processCodeSummary');
      this.processCodeSummary = JSON.parse(retrievedObject);
      console.log("this.processCodeSummary", this.processCodeSummary);
      const extCode = this.processCodeSummary.extSysCode;
      const extSysName = this.processCodeSummary.extSysName;
      const minlimit = this.processCodeSummary.maxUploadLimit;
      const process = this.processCodeSummary.processCode;
      const treasuryrefReq = this.processCodeSummary.treasuryrefRequired;
      const autoUpload = this.processCodeSummary.autoUpload;
      const backDateAllow = this.processCodeSummary.backDateAllow;
      const fCYRateVariance = this.processCodeSummary.fCYRateVariance;
      const sftpRequired = this.processCodeSummary.sftpRequired;
      const futureDateAllow = this.processCodeSummary.futureDateAllow;
      const wuFile = this.processCodeSummary.wuFile;
      this.processCodeEditForm.controls.wuFile.setValue(wuFile);
      const mandatoryFieldImposed = this.processCodeSummary.mandatoryFieldImposed;
      this.processCodeEditForm.controls.mandatoryFieldImposed.setValue(mandatoryFieldImposed);
      const acft = this.processCodeSummary.acft;
      this.processCodeEditForm.controls.acft.setValue(acft);
      this.processCodeEditForm.controls.extSysCode.setValue(extCode);
      this.processCodeEditForm.controls.extSysName.setValue(extSysName);
      this.processCodeEditForm.controls.processCode.setValue(process);
      this.processCodeEditForm.controls.maxUploadLimit.setValue(minlimit);
      this.processCodeEditForm.controls.treasuryrefRequired.setValue(treasuryrefReq);
      this.processCodeEditForm.controls.autoUpload.setValue(autoUpload);
      this.processCodeEditForm.controls.backDateAllow.setValue(backDateAllow);
      this.processCodeEditForm.controls.fCYRateVariance.setValue(fCYRateVariance);
      this.processCodeEditForm.controls.sftpRequired.setValue(sftpRequired);
      this.processCodeEditForm.controls.futureDateAllow.setValue(futureDateAllow);
      this.processCodeEditForm.controls.id.setValue(this.processCodeSummary.id);
      this.processCodeEditForm.controls.recordStatus.setValue(this.processCodeSummary.recordStatus);
      this.processCodeEditForm.controls.verifiedOnce.setValue(this.processCodeSummary.verifiedOnce);
      this.processCodeEditForm.controls.versionNo.setValue(this.processCodeSummary.versionNo);
      this.processCodeEditForm.controls.verifierId.setValue(this.processCodeSummary.verifierId);
      this.processCodeEditForm.controls.verifierDtStamp.setValue(this.processCodeSummary.verifierDtStamp);

      if (autoUpload == true) {
        this.showFilefield = true;
        const fileName = this.processCodeSummary.fileName;
        const filePath = this.processCodeSummary.filePath;
        this.processCodeEditForm.controls.fileName.setValue(fileName);
        this.processCodeEditForm.controls.filePath.setValue(filePath);
        this.processCodeEditForm.controls['fileName'].setValidators([Validators.required]);
        this.processCodeEditForm.controls['filePath'].setValidators([Validators.required]);
      }
      if (backDateAllow == true) {
        this.showbackDateValue = true;
        const bdav = this.processCodeSummary.bdav;
        this.processCodeEditForm.controls.bdav.setValue(bdav);
        this.processCodeEditForm.controls['bdav'].setValidators([Validators.required]);
       
      }
      if (fCYRateVariance == true) {
        this.showfCYExchangeValue = true;
        const fCYPercentage = this.processCodeSummary.fCYPercentage;
        this.processCodeEditForm.controls.fCYPercentage.setValue(fCYPercentage);
        this.processCodeEditForm.controls['fCYPercentage'].setValidators([Validators.required]);
      }
      if (futureDateAllow == true) {
        this.showFutureDate = true;
        const fdav = this.processCodeSummary.fdav;
        this.processCodeEditForm.controls.fdav.setValue(fdav);
        this.processCodeEditForm.controls['fdav'].setValidators([Validators.required]);
      }
      if (sftpRequired == true) {
        this.showsftpValues = true;
        const ipAddress = this.processCodeSummary.sftpIpaddress;
        this.processCodeEditForm.controls.sftpIpaddress.setValue(ipAddress);

        const userName = this.processCodeSummary.sftpUsername;
        this.processCodeEditForm.controls.sftpUsername.setValue(userName);

        const path = this.processCodeSummary.sftpPath;
        this.processCodeEditForm.controls.sftpPath.setValue(path);

        const port = this.processCodeSummary.sftpPort;
        this.processCodeEditForm.controls.sftpPort.setValue(port);

        const password = this.processCodeSummary.sftpPassword;
        this.processCodeEditForm.controls.sftpPassword.setValue(password);

        this.processCodeEditForm.controls['sftpIpaddress'].setValidators([Validators.required]);
        this.processCodeEditForm.controls['sftpPath'].setValidators([Validators.required]);
        this.processCodeEditForm.controls['sftpUsername'].setValidators([Validators.required]);
        this.processCodeEditForm.controls['sftpPort'].setValidators([Validators.required]);
        this.processCodeEditForm.controls['sftpPassword'].setValidators([Validators.required]);
        
      }

      this.auditLog();
    });
  }
  enableEditFlag() {
    this.editFlag = true;
  }
  getextSysCodeData() {
    this.apiService.getAllExt().subscribe(data => {
      this.extSysCodeData = data;

    });
  }
  onOptionsChange(event) {
    const value = event.target.value;
    console.log("Value", value);
    if (value) {
      this.processCodeEditForm.controls.extSysCode.setValue(value);
      this.processCodeEditForm.controls.extSysName.setValue(value);
    }
  }
  treasuryRequired(values: any) {
    const value = values.currentTarget.checked;
    this.processCodeEditForm.controls.treasuryrefRequired.setValue(value);
    this.onCheckboxChange();
  }
  backdatingRequired(backData) {
    console.log("backData", backData);
    this.backDateValue = backData.currentTarget.checked;
    console.log("Back data", this.backDateValue)

    if (this.backDateValue == true) {
      this.showbackDateValue = true;
      this.processCodeEditForm.controls.backDateAllow.setValue(this.backDateValue);
      this.processCodeEditForm.controls['bdav'].setValidators([Validators.required]);
    }
    else {
      this.showbackDateValue = false;
      this.processCodeEditForm.controls['bdav'].reset();
      this.processCodeEditForm.controls.backDateAllow.setValue(this.backDateValue);
      this.processCodeEditForm.controls['bdav'].clearValidators();
    }
    this.onCheckboxChange();
    this.processCodeEditForm.controls['bdav'].updateValueAndValidity();
  }
  autoUploadRequired(values) {
    console.log("autoUploadRequired", values);
    this.autoUploadValue = values.currentTarget.checked;
    console.log("File", this.autoUploadValue)
    if (this.autoUploadValue == true) {
      this.showFilefield = true;
      this.processCodeEditForm.controls.autoUpload.setValue(this.autoUploadValue);
      this.processCodeEditForm.controls['fileName'].setValidators([Validators.required]);
      this.processCodeEditForm.controls['filePath'].setValidators([Validators.required]);
    }
    else {
      this.showFilefield = false;
      this.processCodeEditForm.controls.autoUpload.setValue(this.autoUploadValue);
      this.processCodeEditForm.controls['fileName'].reset();
      this.processCodeEditForm.controls['filePath'].reset();
      this.processCodeEditForm.controls['fileName'].clearValidators();
      this.processCodeEditForm.controls['filePath'].clearValidators();
    }
    this.onCheckboxChange();
    this.processCodeEditForm.controls['fileName'].updateValueAndValidity();
    this.processCodeEditForm.controls['filePath'].updateValueAndValidity();
  }

 
  canExit() : Promise<boolean> {
    let isExit :boolean = false;
    if (this.processCodeEditForm.touched && this.formTouched==true) {
    return Swal.fire({ 
      text: 'There are unsaved changes in the screen. Would you like to navigate to other screen?',
      showCancelButton: true,
      confirmButtonColor: '#E6224A',
      cancelButtonColor: '#011945',
      cancelButtonText: 'NO',
      confirmButtonText: 'YES',
      icon: 'info',
    }).then((result) => {
      if (result.isConfirmed === true) {
        isExit=true;
        this.cdr.markForCheck();
           return isExit;
      }
      else{
        isExit=false;
        this.cdr.markForCheck();
        return isExit;
      }
    
    });
  }
 else{
  isExit=true;
  return new Promise((resolve, reject) => {
    if (isExit === true) {
      isExit=true;
      this.cdr.markForCheck();
      resolve(true);
    }
  
  })
 }
    }
 
  futureDateAllowRequired(futuredataValue) {
    this.futuredataValue = futuredataValue.currentTarget.checked;
    console.log("values", this.futuredataValue)
    if (this.futuredataValue == true) {
      this.showFutureDate = true;
      this.processCodeEditForm.controls.futureDateAllow.setValue(this.futuredataValue);
      this.processCodeEditForm.controls['fdav'].setValidators([Validators.required]);
    }
    else {
      this.showFutureDate = false;
      this.processCodeEditForm.controls['fdav'].reset();
      this.processCodeEditForm.controls.futureDateAllow.setValue(this.futuredataValue);
      
      this.processCodeEditForm.controls['fdav'].clearValidators();
    }
    this.onCheckboxChange();
    this.processCodeEditForm.controls['fdav'].updateValueAndValidity();

  }
  fCYExchangeRequired(fcyValue) {
    
    this.fCYExchangeValue = fcyValue.currentTarget.checked;
    if (this.fCYExchangeValue == true) {
      this.showfCYExchangeValue = true;
      this.processCodeEditForm.controls.fCYRateVariance.setValue(this.fCYExchangeValue);
      this.processCodeEditForm.controls['fCYPercentage'].setValidators([Validators.required]);
    }
    else {
      this.showfCYExchangeValue = false;
      this.processCodeEditForm.controls['fCYPercentage'].reset();
      this.processCodeEditForm.controls.fCYRateVariance.setValue(this.fCYExchangeValue);
      this.processCodeEditForm.controls['fCYPercentage'].clearValidators();
    }
    this.onCheckboxChange();
    this.processCodeEditForm.controls['fCYPercentage'].updateValueAndValidity();
  }

  sftpRequired(sftpValue) {
    this.sftpValues = sftpValue.currentTarget.checked;
    if (this.sftpValues == true) {
      this.showsftpValues = true;
      this.processCodeEditForm.controls.sftpRequired.setValue(this.sftpValues);
      this.processCodeEditForm.controls['sftpUsername'].setValidators([Validators.required]);
      this.processCodeEditForm.controls['sftpPassword'].setValidators([Validators.required]);
      this.processCodeEditForm.controls['sftpIpaddress'].setValidators([Validators.required]);
      this.processCodeEditForm.controls['sftpPort'].setValidators([Validators.required]);
      this.processCodeEditForm.controls['sftpPath'].setValidators([Validators.required]);
    }
    else {
      this.showsftpValues = false;
      this.processCodeEditForm.controls.sftpRequired.setValue(this.sftpValues);
      this.processCodeEditForm.controls['sftpUsername'].reset();
      this.processCodeEditForm.controls['sftpPassword'].reset();
      this.processCodeEditForm.controls['sftpIpaddress'].reset();
      this.processCodeEditForm.controls['sftpPort'].reset();
      this.processCodeEditForm.controls['sftpPath'].reset();
      this.processCodeEditForm.controls['sftpUsername'].clearValidators();
      this.processCodeEditForm.controls['sftpPassword'].clearValidators();
      this.processCodeEditForm.controls['sftpIpaddress'].clearValidators();
      this.processCodeEditForm.controls['sftpPort'].clearValidators();
      this.processCodeEditForm.controls['sftpPath'].clearValidators();
    }
    this.processCodeEditForm.controls['sftpUsername'].updateValueAndValidity();
    this.processCodeEditForm.controls['sftpPassword'].updateValueAndValidity();
    this.processCodeEditForm.controls['sftpIpaddress'].updateValueAndValidity();
    this.processCodeEditForm.controls['sftpPort'].updateValueAndValidity();
    this.processCodeEditForm.controls['sftpPath'].updateValueAndValidity();
  }
  finalSubmitofEditForm(formValue) {
    this.processCodeEditForm.controls.recordStatus.setValue('O');
    this.processCodeEditForm.controls.creatorId.setValue(this.currentUser);
    this.apiService
      .updateExtSys(this.processCodeEditForm.value)
      .subscribe((resp) => {
        
        this.processCodeSummary  = resp;
        if (this.processCodeSummary ) {
          Swal.fire({
            title: 'Record is Updated',
            icon: 'success'
          });
          this.submitEdit=true;
          this.editFlag=false;
          this.formTouched=!this.processCodeEditForm.touched;
          this.processCodeEditForm.markAsUntouched();
          this.processCodeEditForm.markAsPristine();
          this.auditLog();
        }
        else {
          Swal.fire({
            title: 'Failed to Update Data!',
            icon: 'error'
          });
        }
      },
        error => {
          if (HttpErrorResponse) {
            Swal.fire({
              text: 'Server Error!',
              icon: 'error'
            });
          }
        });


  }
  onClickOfAuth() {
    if (this.processCodeSummary.creatorId == this.currentUser) {
      console.log(this.processCodeSummary)
      Swal.fire({
        title: 'Maker Cannot Authorized the Record!',
        icon: 'error'
      });
    } else {
      Swal.fire(
        {
          //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
          text: 'You are trying to Authorize the record. ' + ' Do you want to proceed?',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          // confirmButtonText: 'PROCEED.'
          cancelButtonText: 'NO',
          confirmButtonText: 'YES', 'icon': 'info'
        }
      ).then((result => {
        console.log("this is reopen ", result);
        if (result.isConfirmed === true) {
          let payload:any={}
      
        payload.externalSystemName= this.processCodeSummary.extSysName;
        payload.loggedInUser=this.currentUser,
        payload.processCode=this.processCodeSummary.processCode;
      this.extName =  this.processCodeSummary.extSysName;
      this.apiService.authProcessCodeRecord(payload).subscribe(authResp => {
        this.processCodeSummary = authResp;
        console.log("Auth",  this.processCodeSummary );
        Swal.fire({
          title: ' Record is Authorized',
          icon: 'success'
        });
        this.auditLog();
      })
    }}));
    }
  }
  onClickOfClose() {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Close the record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      console.log("this is reopen ", result);
      if (result.isConfirmed === true) {

    this.processCodeSummary.modifiedBy = this.currentUser;
    this.extName =  this.processCodeSummary.extSysName;
    let payload:any={}
      
    payload.externalSystemName= this.processCodeSummary.extSysName;
    payload.loggedInUser=this.currentUser,
    payload.processCode=this.processCodeSummary.processCode;
    this.apiService.closingProcessCodeRecord(payload).subscribe(closeResp => {
      console.log(closeResp);
      this.processCodeSummary = closeResp;
      if (this.processCodeSummary) {
        Swal.fire({
          title: 'Record is Closed',
          icon: 'success'
        });
        this.auditLog();
      }
      else {
        Swal.fire({
          title: 'Failed to Close the Recored!',
          icon: 'error'
        });
      }
    },
      error => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });
    }}))
  }
  onClickOfReopen() {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Reopen the record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      console.log("this is reopen ", result);
      if (result.isConfirmed === true) {

    this.extName =  this.processCodeSummary.extSysName;
    let payload:any={}
      
    payload.externalSystemName= this.processCodeSummary.extSysName;
    payload.loggedInUser=this.currentUser,
    payload.processCode=this.processCodeSummary.processCode;
    this.apiService.reopeningProcessCodeRecord(payload).subscribe(openResp => {
      console.log(openResp);
      this.processCodeSummary = openResp;
      if (this.processCodeSummary) {
        Swal.fire({
          title: 'Record is Reopened',
          icon: 'success'
        });
        this.auditLog();
      }
      else {
        Swal.fire({
          title: 'Failed to Reoped the Recored!',
          icon: 'error'
        });
      }
    },
      error => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });
    }}));

  }
 
  OndeleteofUser() {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Delete the record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      console.log("this is reopen ", result);
      if (result.isConfirmed === true) {
    this.extName =  this.processCodeSummary.extSysName;
    this.apiService.deleteProcessRecord(this.extName,this.processCodeSummary.processCode,this.currentUser).subscribe(resp => {
      console.log(resp);
      if (resp === "extSys has been deleted!") {
        Swal.fire({
          title: 'Record is Deleted',
          icon: 'success'
        });
        this.router.navigateByUrl('/external-system/processCodeMapping');
      } else {
        Swal.fire({
          text: 'Failed to Delete User Data!',
          icon: 'error'
        });
      }
    },error=>{
      console.log(error.error.message)
    });
  }}));
  }
  handleInput(event) {
    if (event.target.selectionStart === 0 && event.code === 'Space'){
      event.preventDefault();
    }
}
}
