import { HttpErrorResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ChangeDetectorRef } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Observable, of, Subject } from 'rxjs';
import { CanDeactivateGuard, IDeactivateComponent } from 'src/app/shared/guards/can-deactivate.guard';
import { ApiService } from 'src/app/shared/services/api.service';
import { ToastService } from 'src/app/shared/services/toast.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'npr-external-system-new',
  templateUrl: './external-system-new.component.html',
  styleUrls: ['./external-system-new.component.scss']
})

export class ExternalSystemNewComponent implements OnInit,IDeactivateComponent {
  allDeptNames: any;
  externalSystemSummry: any;
  externalSystemForm: FormGroup;
  externalSystem = {
    extSysCode: '',
    extSysName: '',
    department: '',
    modifiedBy: ''
  }
  currentUser: string;
  externalSystemResponse: any;
  editFlag: boolean=false;
  submitBtn: boolean=true;
  space: boolean=false;
  var: any;
  spaceForExtName: boolean=false;
  formTouched: boolean=true;
  submit:boolean;
  showSubmit: boolean = false;
  extName: any;
  submitDisable: boolean;
  saveEditFlag: boolean = false;
  externalSystemUpdate = {
    extSysCode: '',
    extSysName: '',
    department: '',
    modifiedBy: '',
    authStatus: '',
    authorizedBy: '',
    authorizedTime: '',
    modifiedTime: '',
    recordStatus: '',
    updatedBy: '',
    verifiedOnce: '',
    version: '',
  }

  constructor(private apiService: ApiService, private formBuilder: FormBuilder, private cdr: ChangeDetectorRef,
    private toastService: ToastService,private canDeactivateGuard: CanDeactivateGuard,private router : Router) { }

  ngOnInit(): void {
    this.currentUser = localStorage.getItem("userFromLogin");
    this.externalSystemForm = this.formBuilder.group({
      extSysCode: ["", [Validators.required]],
      extSysName: ["", [Validators.required]],
      department: ["", [Validators.required]],
      verifiedBy: [""],
      verifiedTime: [""],
      recordStatus: [""],
      verifiedOnce: [""],
      modNo: [""],
      verifiedStatus: [""],
      creatorDtStamp: [""],
      creatorId: [""],
    });
    this.getAllDeptNames();
  }
  getAllDeptNames() {
    this.apiService.getAllDept().subscribe(deptData => {
      this.allDeptNames = deptData.sort();
    });
  }
  onOptionsChange(event) {
    
    if (event) {
      this.externalSystemForm.controls.department.setValue(event);
    }
  }
 
 
  canExit() : Promise<boolean> {
    let isExit :boolean = false;
    if (this.externalSystemForm.touched && this.formTouched==true) {
    return Swal.fire({ 
      text: 'There are unsaved changes in the screen. Would you like to navigate to other screen?',
      showCancelButton: true,
      confirmButtonColor: '#E6224A',
      cancelButtonColor: '#011945',
      cancelButtonText: 'NO',
      confirmButtonText: 'YES',
      icon: 'info',
    }).then((result) => {
      if (result.isConfirmed === true) {
        isExit=true;
        this.cdr.markForCheck();
           return isExit;
      }
      else{
        isExit=false;
        this.cdr.markForCheck();
        return isExit;
      }
    
    });
  }
 else{
  isExit=true;
  return new Promise((resolve, reject) => {
    if (isExit === true) {
      isExit=true;
      this.cdr.markForCheck();
      resolve(true);
    }
  
  })
 }
    }
  auditLog(){
    if (this.externalSystemResponse.authStatus === 'U') {
      this.externalSystemResponse.authStatus = 'UNAUTHORIZED'
    }
    if (this.externalSystemResponse.authStatus === 'A') {
      this.externalSystemResponse.authStatus = 'AUTHORIZED'
    }
    if (this.externalSystemResponse.recordStatus === 'O') {
      this.externalSystemResponse.recordStatus = 'OPEN'
    }
    if (this.externalSystemResponse.recordStatus === 'C') {
      this.externalSystemResponse.recordStatus = 'CLOSED'
    }
    if (this.externalSystemResponse.verifiedOnce === 'N') {
      this.externalSystemResponse.verifiedOnce = 'NO'
    }
    if (this.externalSystemResponse.verifiedOnce === 'Y') {
      this.externalSystemResponse.verifiedOnce = 'YES'
    }
  }
  onSubmit(externalSystemFormValue) {
    this.externalSystem.extSysCode = externalSystemFormValue.extSysCode;
    this.externalSystem.extSysName = externalSystemFormValue.extSysName;
    this.externalSystem.department = externalSystemFormValue.department;
    this.externalSystem.modifiedBy = this.currentUser;
    this.apiService.saveExternalSystem(this.externalSystem).subscribe(res => {
      this.externalSystemResponse = res;
    if (this.externalSystemResponse != null) {
      this.editFlag=true;
      this.submitBtn=false;
      Swal.fire({
        title: 'Record is Created',
        icon: 'success'
      });
      this.submitDisable=false;
      this.submit = true;
      this.auditLog();
      this.formTouched=!this.externalSystemForm.touched;
      // this.externalSystemForm.reset();
    }
    else {
      Swal.fire({
        text: 'Given External System Code/External System Already Present!',
        icon: 'warning'
      });
    }
  },
    error => {
      if (HttpErrorResponse) {
        Swal.fire({
          text: 'Server Error',
          icon: 'error'
        });
      }
    });
  }
  getExternalSystemSummry() {
    this.apiService.getExternalSystemSummry().subscribe((summaryData) => {
      this.externalSystemSummry = summaryData;
      var retrievedObject = localStorage.getItem('externalSystemSummry');
      this.externalSystemSummry = JSON.parse(retrievedObject);
      // console.log("this.externalSystemSummry", this.externalSystemSummry);
      const extCode = this.externalSystemSummry.extSysCode;
      const extName = this.externalSystemSummry.extSysName;
      const department = this.externalSystemSummry.department;
      this.externalSystemForm.controls.extSysCode.setValue(extCode);
      this.externalSystemForm.controls.extSysName.setValue(extName);
      this.externalSystemForm.controls.department.setValue(department);
      this.auditLog();
    });
  }
  onSubmitOfEdit(externalSystemFormValue) {
    this.externalSystemUpdate.extSysCode = externalSystemFormValue.extSysCode;
    this.externalSystemUpdate.extSysName = externalSystemFormValue.extSysName;
    this.externalSystemUpdate.department = externalSystemFormValue.department;
    this.externalSystemUpdate.modifiedBy = this.currentUser;
    this.getExternalSystemSummry();
    this.externalSystemUpdate.authStatus = this.externalSystemSummry.authStatus;
    this.externalSystemUpdate.authorizedBy = this.externalSystemSummry.authorizedBy;
    this.externalSystemUpdate.authorizedTime = this.externalSystemSummry.authorizedTime;
    this.externalSystemUpdate.modifiedTime = this.externalSystemSummry.modifiedTime;
    this.externalSystemUpdate.recordStatus = this.externalSystemSummry.recordStatus;
    this.externalSystemUpdate.updatedBy = this.currentUser;
    this.externalSystemUpdate.verifiedOnce = this.externalSystemSummry.verifiedOnce;
    this.externalSystemUpdate.version = this.externalSystemSummry.version;

    this.apiService.updateexternalSystemEdit(this.externalSystemUpdate).subscribe(data => {
      this.externalSystemSummry = data;
      if (this.externalSystemSummry) {
        Swal.fire({
          title: 'Record is Updated',
          icon: 'success'
        });
        this.submitDisable=true;
        this.formTouched=!this.externalSystemForm.touched;
        this.auditLog();
      }
      else {
        Swal.fire({
          title: 'Failed to Update Data!',
          icon: 'error'
        });
      }
    },
      error => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });

  }
  enableEditFlag() {
    this.editFlag=false;
    this.saveEditFlag=true;
    this.submit = false;
    this.showSubmit = true;
  }
  onClickOfAuth() {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Authorize record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      console.log("this is reopen ", result);
      if (result.isConfirmed === true) {
        if (this.externalSystemSummry.modifiedBy === this.currentUser) {
          Swal.fire(
            {
              text: "Maker cannot authorize the record",
              icon: 'error'
            }
          );
          return;
        }
        this.apiService.authorizingTheRecordExternal(this.externalSystemSummry, this.currentUser).subscribe(authResp => {
          this.externalSystemSummry = authResp;
          this.auditLog();
          Swal.fire({
            title: 'Record is Authorized ',
            icon: 'success'
          });

        })
        // }
      }
    }))

  }
  updateSubmitofForm(externalSystemFormValue){
    this.externalSystem.extSysCode = externalSystemFormValue.extSysCode;
    this.externalSystem.extSysName = externalSystemFormValue.extSysName;
    this.externalSystem.department = externalSystemFormValue.department;
    this.externalSystem.modifiedBy = this.currentUser;
    this.apiService.saveExternalSystem(this.externalSystem).subscribe(res => {
      this.externalSystemResponse = res;
    if (this.externalSystemResponse != null) {
      this.editFlag=true;
      this.submitBtn=false;
      Swal.fire({
        title: 'Record is Created',
        icon: 'success'
      });
      this.submit = true;
      this.showSubmit = false;
      this.formTouched=!this.externalSystemForm.touched;
      // this.externalSystemForm.reset();
    }
    else {
      Swal.fire({
        text: 'Given External System Code/External System Already Present!',
        icon: 'warning'
      });
    }
  },
    error => {
      if (HttpErrorResponse) {
        Swal.fire({
          text: 'Server Error',
          icon: 'error'
        });
      }
    });
  }
  OndeleteofUser() {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to delete record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      console.log("this is reopen ", result);
      if (result.isConfirmed === true) {

        this.apiService.onClickOfDeleteOfUpdateExcel(this.externalSystemSummry).subscribe(resp => {
          console.log(resp);
          if (resp === true) {
            Swal.fire({
              title: 'Record is Deleted',
              icon: 'success'
            });
            this.router.navigateByUrl('/external-system/extSys-summary');
          } else {
            Swal.fire({
              title: 'Failed to Delete User Data!',
              icon: 'error'
            });
          }
        });
      }
    }))

  }
    onRest() {
    this.externalSystemForm.reset();
    this.externalSystemResponse = [];
  }
  checkingSpaceForExtCode(extCode)
  {
  
  this.var=extCode.value;
   const result=this.var.trim();
if(result)
{
 this.space=true;
}
else{
 this.space=false;
}
}
checkingSpaceForExtName(extName)
{
this.var=extName.value;
 const result=this.var.trim();
if(result)
{
this.spaceForExtName=true;
}
else{
this.spaceForExtName=false;

}
}
}
