import { HttpErrorResponse } from '@angular/common/http';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Observable, of, Subject } from 'rxjs';
import { CanDeactivateGuard, IDeactivateComponent } from 'src/app/shared/guards/can-deactivate.guard';
import { permissionsLabels } from 'src/app/shared/models/fmosNewRolePermissions';
import { ApiService } from 'src/app/shared/services/api.service';
import { RoleService } from 'src/app/shared/services/role.service';
import { ToastService } from 'src/app/shared/services/toast.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'npr-external-system-edit',
  templateUrl: './external-system-edit.component.html',
  styleUrls: ['./external-system-edit.component.scss']
})
export class ExternalSystemEditComponent implements OnInit ,IDeactivateComponent{
  externalSystemSummry: any;
  externalSystemEditForm: FormGroup
  allDeptNames: any;
  editFlag: boolean = false;
  submitDisable: boolean;
  formTouched: boolean=true;
  externalSystemUpdate = {
    extSysCode: '',
    extSysName: '',
    department: '',
    modifiedBy: '',
    authStatus: '',
    authorizedBy: '',
    authorizedTime: '',
    modifiedTime: '',
    recordStatus: '',
    updatedBy: '',
    verifiedOnce: '',
    version: '',
  }
  currentUser: string;
  roleCodes = new permissionsLabels();
  showDialog = false;
  subject = new Subject<boolean>();
  constructor(private apiService: ApiService, private router: Router,private cdr: ChangeDetectorRef,

    private roleService: RoleService, private formBuilder: FormBuilder, private toastService: ToastService) { }

  ngOnInit(): void {
    setTimeout(() => {
      this.newRolePermissions();
    }, 2000);
    this.roleService.screenLabelList.subscribe(message => this.roleCodes = message);
    setTimeout(() => {
      console.log(this.roleCodes);
    }, 3000);
   console.log("role codes",this.roleCodes)
    this.currentUser = localStorage.getItem("userFromLogin");
    this.externalSystemEditForm = this.formBuilder.group({
      extSysCode: ["", [Validators.required]],
      extSysName: ["", [Validators.required]],
      department: ["", [Validators.required]],
      verifiedBy: [""],
      verifiedTime: [""],
      recordStatus: [""],
      verifiedOnce: [""],
      modNo: [""],
      verifiedStatus: [""],
      creatorDtStamp: [""],
      creatorId: [""],
    });
    this.getAllDeptNames();
    this.getExternalSystemSummry();
  }
  newRolePermissions()
  {
    this.roleService.fetchScreenPermissions('External System');
  }
  getAllDeptNames() {
    this.apiService.getAllDept().subscribe(deptData => {
      this.allDeptNames = deptData;
    });
  }
  auditLog() {
    if (this.externalSystemSummry.authStatus === 'U' || this.externalSystemSummry.authStatus === 'Unauthorized') {
      this.externalSystemSummry.authStatus = 'UNAUTHORIZED'
    }
    if (this.externalSystemSummry.authStatus === 'A' || this.externalSystemSummry.authStatus == 'Authorized') {
      this.externalSystemSummry.authStatus = 'AUTHORIZED'
    }
    if (this.externalSystemSummry.recordStatus === 'O' || this.externalSystemSummry.recordStatus === 'Open') {
      this.externalSystemSummry.recordStatus = 'OPEN'
    }
    if (this.externalSystemSummry.recordStatus === 'C' || this.externalSystemSummry.recordStatus === 'Close') {
      this.externalSystemSummry.recordStatus = 'CLOSED'
    }
    if (this.externalSystemSummry.verifiedOnce === 'N' || this.externalSystemSummry.verifiedOnce === 'No') {
      this.externalSystemSummry.verifiedOnce = 'NO'
    }
    if (this.externalSystemSummry.verifiedOnce === 'Y' || this.externalSystemSummry.verifiedOnce === 'Yes') {
      this.externalSystemSummry.verifiedOnce = 'YES'
    }
  }

  canExit() : Promise<boolean> {
    let isExit :boolean = false;
    if (this.externalSystemEditForm.touched && this.formTouched==true) {
    return Swal.fire({ 
      text: 'There are unsaved changes in the screen. Would you like to navigate to other screen?',
      showCancelButton: true,
      confirmButtonColor: '#E6224A',
      cancelButtonColor: '#011945',
      cancelButtonText: 'NO',
      confirmButtonText: 'YES',
      icon: 'info',
    }).then((result) => {
      if (result.isConfirmed === true) {
        isExit=true;
        this.cdr.markForCheck();
           return isExit;
      }
      else{
        isExit=false;
        this.cdr.markForCheck();
        return isExit;
      }
    
    });
  }
 else{
  isExit=true;
  return new Promise((resolve, reject) => {
    if (isExit === true) {
      isExit=true;
      this.cdr.markForCheck();
      resolve(true);
    }
  
  })
 }
    }
 
 

  getExternalSystemSummry() {
    this.apiService.getExternalSystemSummry().subscribe((summaryData) => {
      this.externalSystemSummry = summaryData;
      var retrievedObject = localStorage.getItem('externalSystemSummry');
      this.externalSystemSummry = JSON.parse(retrievedObject);
      // console.log("this.externalSystemSummry", this.externalSystemSummry);
      const extCode = this.externalSystemSummry.extSysCode;
      const extName = this.externalSystemSummry.extSysName;
      const department = this.externalSystemSummry.department;
      this.externalSystemEditForm.controls.extSysCode.setValue(extCode);
      this.externalSystemEditForm.controls.extSysName.setValue(extName);
      this.externalSystemEditForm.controls.department.setValue(department);
      this.auditLog();
    });
  }
  onOptionsChange(event) {
    const value = event.target.value;
    if (value) {
      this.externalSystemEditForm.controls.department.setValue(value);
    }
  }
  enableEditFlag() {
    this.editFlag = true;
  }
  onSubmit(externalSystemEditForm) {
    this.externalSystemUpdate.extSysCode = externalSystemEditForm.extSysCode;
    this.externalSystemUpdate.extSysName = externalSystemEditForm.extSysName;
    this.externalSystemUpdate.department = externalSystemEditForm.department;
    this.externalSystemUpdate.modifiedBy = this.currentUser;
    this.externalSystemUpdate.authStatus = this.externalSystemSummry.authStatus;
    this.externalSystemUpdate.authorizedBy = this.externalSystemSummry.authorizedBy;
    this.externalSystemUpdate.authorizedTime = this.externalSystemSummry.authorizedTime;
    this.externalSystemUpdate.modifiedTime = this.externalSystemSummry.modifiedTime;
    this.externalSystemUpdate.recordStatus = this.externalSystemSummry.recordStatus;
    this.externalSystemUpdate.updatedBy = this.currentUser;
    this.externalSystemUpdate.verifiedOnce = this.externalSystemSummry.verifiedOnce;
    this.externalSystemUpdate.version = this.externalSystemSummry.version;

    this.apiService.updateexternalSystemEdit(this.externalSystemUpdate).subscribe(data => {
      this.externalSystemSummry = data;
      if (this.externalSystemSummry) {
        Swal.fire({
          title: 'Record is Updated',
          icon: 'success'
        });
        this.submitDisable=true;
        this.editFlag=false;
        this.formTouched=!this.externalSystemEditForm.touched;
        this.externalSystemEditForm.markAsUntouched();
        this.externalSystemEditForm.markAsPristine();
        this.auditLog();
      }
      else {
        Swal.fire({
          title: 'Failed to Update Data!',
          icon: 'error'
        });
      }
    },
      error => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });

  }


  onClickOfAuth() {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Authorize record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      console.log("this is reopen ", result);
      if (result.isConfirmed === true) {
        if (this.externalSystemSummry.modifiedBy === this.currentUser) {
          Swal.fire(
            {
              text: "Maker cannot authorize the record",
              icon: 'error'
            }
          );
          return;
        }
        this.apiService.authorizingTheRecordExternal(this.externalSystemSummry, this.currentUser).subscribe(authResp => {
          this.externalSystemSummry = authResp;
          this.auditLog();
          Swal.fire({
            title: 'Record is Authorized ',
            icon: 'success'
          });

        })
        // }
      }
    }))

  }


  onClickOfClose() {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to close record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      console.log("this is reopen ", result);
      if (result.isConfirmed === true) {
        console.log(this.externalSystemSummry);
        this.externalSystemSummry.modifiedBy = this.currentUser;
        console.log(this.externalSystemSummry.modifiedBy);

        this.apiService.closingTheRecord(this.externalSystemSummry).subscribe(closeResp => {
          console.log(closeResp);
          this.externalSystemSummry = closeResp;
          if (this.externalSystemSummry) {
            console.log(this.externalSystemSummry);
            Swal.fire({
              title: 'Record is  Closed',
              icon: 'success'
            });
            this.auditLog();
          }
          else {
            Swal.fire({
              title: 'Failed to Close the Recored!',
              icon: 'error'
            });
          }
        },
          error => {
            if (HttpErrorResponse) {
              Swal.fire({
                text: 'Server Error!',
                icon: 'error'
              });
            }
          });
      }
    }))

  }


  onClickOfReopen() {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to re-open record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      console.log("this is reopen ", result);
      if (result.isConfirmed === true) {
        this.apiService.reopeningTheRecord(this.externalSystemSummry).subscribe(openResp => {
          console.log(openResp);
          this.externalSystemSummry = openResp;
          if (this.externalSystemSummry) {
            Swal.fire({
              title: 'Record is Reopened',
              icon: 'success'
            });
            this.auditLog();
          }
          else {
            Swal.fire({
              title: 'Failed to Reopen the Record!',
              icon: 'error'
            });
          }
        },
          error => {
            if (HttpErrorResponse) {
              Swal.fire({
                text: 'Server Error!',
                icon: 'error'
              });
            }
          });
      }
    }))

  }
  OndeleteofUser() {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to delete record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      console.log("this is reopen ", result);
      if (result.isConfirmed === true) {

        this.apiService.onClickOfDeleteOfUpdateExcel(this.externalSystemSummry).subscribe(resp => {
          console.log(resp);
          if (resp === true) {
            Swal.fire({
              title: 'Record is Deleted',
              icon: 'success'
            });
            this.router.navigateByUrl('/external-system/extSys-summary');
          } else {
            Swal.fire({
              title: 'Failed to Delete User Data!',
              icon: 'error'
            });
          }
        });
      }
    }))

  }

}
