import { Injectable } from '@angular/core';
import { AppConstants } from 'src/app/shared/config/app.constant';
import { HttpClient } from '@angular/common/http';
import { FcyDealNumber } from './FcyDealNumber';
export const API_URL = AppConstants.baseURL;

@Injectable({
  providedIn: 'root'
})
export class FcyDealNumberService {
  constructor(private http:HttpClient) { }
  fetchingBatchNo()
  {
    return this.http.get(`${API_URL}/medUpload/getModule`);
  }

  fetchingBatchDeletion()
  {
    return this.http.get(`${API_URL}/batchDelete/getTodaysBatches`);
  }
  sendingBatchNumber(batchNo)
  {console.log(batchNo);
    return this.http.get<any>(`${API_URL}/medUpload/get/${batchNo}`);
  }
  sendingBatchDeletion(batchNo)
  {console.log(batchNo);
    return this.http.get<any>(`${API_URL}/batchDelete/getBatchNoDetail/${batchNo}`);
  }
  sendingBatchDeletionAuth(batchNo)
  {console.log(batchNo);
    return this.http.get<any>(`${API_URL}/batchDelete/checkAuthStatus/${batchNo}`);
  }
  onSaveOfFcyDealNumber(fcydata)
  {
    return this.http.put<FcyDealNumber>(`${API_URL}/medUpload/modify`,fcydata);
  }
  RequestForDeletion(batchNo,userId)
  {console.log(batchNo);
    return this.http.get<boolean>(`${API_URL}/batchDelete/requestForDeletion/${batchNo}/${userId}`);
  }
  deletion(batchNo,userId)
  {
    console.log(batchNo);
    return this.http.get<any>(`${API_URL}/batchDelete/delete/${batchNo}/${userId}`);
  }
}
