import { HttpErrorResponse } from '@angular/common/http';
import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { AbstractControl, FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { even } from '@rxweb/reactive-form-validators';
import { data } from 'jquery';
import { Observable, of, Subject } from 'rxjs';
import { CanDeactivateGuard, IDeactivateComponent } from 'src/app/shared/guards/can-deactivate.guard';
import { ApiService } from 'src/app/shared/services/api.service';
import { ToastService } from 'src/app/shared/services/toast.service';
import Swal from 'sweetalert2';
import { FcyDealNumberService } from './fcy-deal-number.service';

@Component({
  selector: 'npr-treasury-reference',
  templateUrl: './treasury-reference.component.html',
  styleUrls: ['./treasury-reference.component.scss'],
})
export class TreasuryReferenceComponent implements OnInit ,IDeactivateComponent{
  UdfForm: FormGroup;
  userDefinedFields: FormArray;
  selectedBatchNumber: any;
  treasuryReferenceForm: FormGroup;
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject<any>();
  showTable: boolean = false;
  treasuryReferenceData: any;
  disableSave: boolean;
  isSelected: boolean = true;
  isLoading: boolean = false;
  constructor(
    private apiService: ApiService,
    private fb: FormBuilder,
    private toastService: ToastService,
    private fcyService: FcyDealNumberService,
    private cdr: ChangeDetectorRef
  ) {}

  ngOnInit(): void {
    this.treasuryReferenceForm = this.fb.group({
      batch: ['', [Validators.required]],
    });
    this.UdfForm = this.fb.group({
      userDefinedFields: this.fb.array([this.createUserDefinedFields()]),
    });

    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 5,
      processing: true,
      lengthMenu: [
        [5, 10, 20, 30,-1],
        [5, 10, 20, 30,"ALL"],
      ],
    };

    this.fetchingBatchNumber();
  }
  createUserDefinedFields1(data): any {
    console.log(data);
    console.log(data.length);
    for(let i = 0 ;i<=data.length;i++){
      console.log(i);
      // this.UdfForm.get('userDefinedFields')['controls'].acumenJournal.setValue(this.treasuryReferenceData[i].acumenJournal);

      return this.fb.group({
        acumenJournal: [ data[i].acumenJournal? data[i].acumenJournal:"", [Validators.required]],
      });
    }
   
  }
  createUserDefinedFields(): any {
    return this.fb.group({
      acumenJournal: [ "", [Validators.required]],
    });
  }
  tempArrayList: any[] = [];
  inputReference(event,index){
    console.log(event,"+",index);
      this.treasuryReferenceData[index].acumenJournal=event.target.value;
  }
  onSaveOfFcyDealNumber(data) {
  data=this.treasuryReferenceData;
    console.log('onSaveOfFcyDealNumber', data);
    this.fcyService.onSaveOfFcyDealNumber(data).subscribe((saveResp) => {
      console.log(saveResp);
      if (saveResp) {
        Swal.fire({
          title: "Record saved successfully",
          icon:"success"
        });
        this.disableSave = true;
      } else {
        Swal.fire({
          title: 'Failed Save the Record',
        });
      }
    });
  }
  fetchingBatchNumber() {
    this.isLoading = true;
    this.apiService.fetchingBatchNo().subscribe((batchNumberResp) => {
      this.selectedBatchNumber = batchNumberResp;
      this.isLoading = false;
      this.cdr.markForCheck();

      this.dtTrigger.next();
      console.log('this.selectedBatchNumber', this.selectedBatchNumber);
    });
  }
  onOptionsChange(event) {
    const value = event.target.value;
    if (value) {
      this.treasuryReferenceForm.controls.batch.setValue(value);
      this.apiService.sendingBatchNumber(value).subscribe((batchResp) => {
        console.log(batchResp);
        if (batchResp == '') {
          Swal.fire({
            title: 'No Data for Batch:' + value,
            icon: 'warning',
          });
          this.showTable = false;
        } else {
          this.treasuryReferenceData = batchResp;
          this.showTable = true;
          // // this.createUserDefinedFields1(this.treasuryReferenceData);
          // this.UdfForm = this.fb.group({
          //   userDefinedFields: this.fb.array([this.createUserDefinedFields1(this.treasuryReferenceData)]),
          // });
          console.log(this.treasuryReferenceData);
          // for(let i = 0;i<=this.treasuryReferenceData.length;i++){
          //   console.log(this.treasuryReferenceData[i].acumenJournal);
          //   console.log("UdfForm: ", this.UdfForm);
            // ((this.UdfForm.get('controls') as FormArray).at(i) as FormGroup).get('userDefinedFields').patchValue(this.treasuryReferenceData[i].acumenJournal);
            // this.UdfForm.controls['userDefinedFields'].setValue(this.treasuryReferenceData[i].acumenJournal);
          // this.UdfForm.get('userDefinedFields')['controls'].push(this.treasuryReferenceData[i].acumenJournal);
          // }
          // controls.userDefinedFields.value[0]
          // this.UdfForm.get('userDefinedFields')['controls'].setValue([this.treasuryReferenceData[0].acumenJournal, this.treasuryReferenceData[0].acumenJournal,this.treasuryReferenceData[0].acumenJournal,this.treasuryReferenceData[0].acumenJournal])
          // this.UdfForm.controls.userDefinedFields.value.setValue([this.treasuryReferenceData[0].acumenJournal, this.treasuryReferenceData[0].acumenJournal,this.treasuryReferenceData[0].acumenJournal,this.treasuryReferenceData[0].acumenJournal]);
        } 
      },
        error => {
          if (HttpErrorResponse) {
            console.log(error)
            Swal.fire({
              text: error.error.text,
              icon: 'error'
            });
          }
        });
     
    }
  }
  get formArrUDF() {
    return this.UdfForm.get("userDefinedFields") as FormArray;
  }
 
  canExit() : Promise<boolean> {
    let isExit :boolean = false;
    if (this.treasuryReferenceForm.touched){
    return Swal.fire({ 
      text: 'There are unsaved changes in the screen. Would you like to navigate to other screen?',
      showCancelButton: true,
      confirmButtonColor: '#E6224A',
      cancelButtonColor: '#011945',
      cancelButtonText: 'NO',
      confirmButtonText: 'YES',
      icon: 'info',
    }).then((result) => {
      if (result.isConfirmed === true) {
        isExit=true;
        this.cdr.markForCheck();
           return isExit;
      }
      else{
        isExit=false;
        this.cdr.markForCheck();
        return isExit;
      }
    
    });
  }
 else{
  isExit=true;
  return new Promise((resolve, reject) => {
    if (isExit === true) {
      isExit=true;
      this.cdr.markForCheck();
      resolve(true);
    }
  
  })
 }
    }
  

  ngOnDestroy(): void {
    this.dtTrigger.unsubscribe();
  }
}
