import { HttpErrorResponse } from '@angular/common/http';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Observable, of, Subject } from 'rxjs';
import { CanDeactivateGuard, IDeactivateComponent } from 'src/app/shared/guards/can-deactivate.guard';
import { ApiService } from 'src/app/shared/services/api.service';
import { ToastService } from 'src/app/shared/services/toast.service';
import Swal from 'sweetalert2';


@Component({
  selector: 'npr-process-code-mapping-new',
  templateUrl: './process-code-mapping-new.component.html',
  styleUrls: ['./process-code-mapping-new.component.scss']
})
export class ProcessCodeMappingNewComponent implements OnInit ,IDeactivateComponent{
  processCodeForm: FormGroup;
  extSysCodeData: any;
  showSubmit: boolean = false;
  showFilefield: boolean = false;
  showFutureDate: boolean = false;
  showfCYExchangeValue: boolean = false;
  showbackDateValue: boolean = false;
  showsftpValues: boolean = false;
  codeerror:boolean=false;
  autoUploadValue: any;
  futuredataValue: any;
  fCYExchangeValue: any
  backDateValue: any;
  backdate: any;
  futuredate:boolean = false;
  backdates:boolean = false;
  sftpValues: any;
  currentUser: string;
  submit: boolean;
  auditlogData: any;
  extName: any;
  nameerror:boolean = false;
  FCYpercent:boolean = false;
  patherror:boolean = false;
  formTouched: boolean=true;
  showDialog = false;
  subject = new Subject<boolean>();
  var: any;
  constructor(private apiService: ApiService, private formBuilder: FormBuilder, private toastService: ToastService,
    private router : Router,private cdr: ChangeDetectorRef) { }

  ngOnInit(): void {
    this.currentUser = localStorage.getItem("userFromLogin");
    this.processCodeForm = this.formBuilder.group({
      extSysCode: ["", [Validators.required]],
      extSysName: ["", [Validators.required]],
      processCode: ["", [Validators.required]],
      maxUploadLimit: ["", [Validators.required]],
      acft: [false],
      autoUpload: [false],
      backDateAllow: [false],
      bdav: [""],
      creatorId: [""],
      fCYPercentage: [""],
      fCYRateVariance: [false],
      fdav: [""],
      fileName: [""],
      filePath: [""],
      futureDateAllow: [false],
      mandatoryFieldImposed: [false],
      sftpIpaddress: [""],
      sftpPassword: [""],
      sftpPath: [""],
      sftpPort: [""],
      sftpRequired: [false],
      sftpUsername: [""],
      treasuryrefRequired: [false],
      wuFile: [false],
    });
    this.getextSysCodeData();

  }
  auditLog() {
    if (this.auditlogData.authStatus =='U') {
      this.auditlogData.authStatus = 'UNAUTHORIZED'
    }
    if (this.auditlogData.authStatus == 'A') {
      this.auditlogData.authStatus = 'AUTHORIZED'
    }
    if (this.auditlogData.recordStatus == 'O') {
      this.auditlogData.recordStatus = 'OPEN'
    }
    if (this.auditlogData.recordStatus == 'C') {
      this.auditlogData.recordStatus = 'CLOSED'
    }
    if (this.auditlogData.verifiedOnce == 'N') {
      this.auditlogData.verifiedOnce = 'NO'
    }
    if (this.auditlogData.verifiedOnce == 'Y') {
      this.auditlogData.verifiedOnce = 'YES'
    }
  }
  getextSysCodeData() {
    this.apiService.getAllAuthExt().subscribe(data => {
      this.extSysCodeData = data;
      console.log(this.extSysCodeData);
    });
  }
  onOptionsChange(event) {
    const value = event;
    console.log("Value", value);
    if (value) {
      this.processCodeForm.controls.extSysCode.setValue(value);
      this.processCodeForm.controls.extSysName.setValue(this.extSysCodeData.filter(item=> item.extSysCode === value)[0].extSysName);
    }
  }
  treasuryRequired(values: any) {
    console.log("values",values);
    const value = values.currentTarget.checked;
    this.processCodeForm.controls.treasuryrefRequired.setValue(value);

  }
 
  canExit() : Promise<boolean> {
    let isExit :boolean = false;
    if (this.processCodeForm.touched && this.formTouched==true) {
    return Swal.fire({ 
      text: 'There are unsaved changes in the screen. Would you like to navigate to other screen?',
      showCancelButton: true,
      confirmButtonColor: '#E6224A',
      cancelButtonColor: '#011945',
      cancelButtonText: 'NO',
      confirmButtonText: 'YES',
      icon: 'info',
    }).then((result) => {
      if (result.isConfirmed === true) {
        isExit=true;
        this.cdr.markForCheck();
           return isExit;
      }
      else{
        isExit=false;
        this.cdr.markForCheck();
        return isExit;
      }
    
    });
  }
 else{
  isExit=true;
  return new Promise((resolve, reject) => {
    if (isExit === true) {
      isExit=true;
      this.cdr.markForCheck();
      resolve(true);
    }
  
  })
 }
    }
 
  backdatingRequired(backData) {
    console.log("backData", backData);
    this.backDateValue = backData.currentTarget.checked;
    console.log("Back data", this.backDateValue)

    if (this.backDateValue == true) {
      this.showbackDateValue = true;
      this.processCodeForm.controls.backDateAllow.setValue(this.backDateValue);
      this.processCodeForm.controls['bdav'].setValidators([Validators.required]);
    }
    else {
      this.showbackDateValue = false;
      this.processCodeForm.controls['bdav'].reset();
      this.processCodeForm.controls.backDateAllow.setValue(this.backDateValue);
      this.processCodeForm.controls['bdav'].clearValidators();
    }
    this.processCodeForm.controls['bdav'].updateValueAndValidity();
  }
 

  
  autoUploadRequired(values) {
    this.autoUploadValue = values.currentTarget.checked;
    console.log("File", this.autoUploadValue);
    if (this.autoUploadValue == true) {
      this.showFilefield = true;
      this.processCodeForm.controls.autoUpload.setValue(this.autoUploadValue);
      this.processCodeForm.controls['fileName'].setValidators([Validators.required]);
      this.processCodeForm.controls['filePath'].setValidators([Validators.required]);
    }
    else {
      this.showFilefield = false;
      this.processCodeForm.controls.autoUpload.setValue(this.autoUploadValue);
      this.processCodeForm.controls['fileName'].reset();
      this.processCodeForm.controls['filePath'].reset();
      this.processCodeForm.controls['fileName'].clearValidators();
      this.processCodeForm.controls['filePath'].clearValidators();
    }
    this.processCodeForm.controls['fileName'].updateValueAndValidity();
    this.processCodeForm.controls['filePath'].updateValueAndValidity();
  }
  futureDateAllowRequired(futuredataValue) {
    this.futuredataValue = futuredataValue.currentTarget.checked;
    
    if (this.futuredataValue == true) {
      this.showFutureDate = true;
      this.processCodeForm.controls.futureDateAllow.setValue(this.futuredataValue);
      this.processCodeForm.controls['fdav'].setValidators([Validators.required]);
    }
    else {
      this.showFutureDate = false;
      this.processCodeForm.controls['fdav'].reset();
      this.processCodeForm.controls.futureDateAllow.setValue(this.futuredataValue);
      this.processCodeForm.controls['fdav'].clearValidators();
    }
    this.processCodeForm.controls['fdav'].updateValueAndValidity();

  }
  fCYExchangeRequired(fcyValue) {
    this.fCYExchangeValue = fcyValue.currentTarget.checked;
    if (this.fCYExchangeValue == true) {
      this.showfCYExchangeValue = true;
      this.processCodeForm.controls.fCYRateVariance.setValue(this.fCYExchangeValue);
      this.processCodeForm.controls['fCYPercentage'].setValidators([Validators.required]);
    }
    else {
      this.showfCYExchangeValue = false;
      this.processCodeForm.controls['fCYPercentage'].reset();
      this.processCodeForm.controls.fCYRateVariance.setValue(this.fCYExchangeValue);
      this.processCodeForm.controls['fCYPercentage'].clearValidators();
    }
    this.processCodeForm.controls['fCYPercentage'].updateValueAndValidity();
  }

  sftpRequired(sftpValue) {
    this.sftpValues = sftpValue.currentTarget.checked;
    if (this.sftpValues == true) {
      this.showsftpValues = true;
      this.processCodeForm.controls.sftpRequired.setValue(this.sftpValues);
      this.processCodeForm.controls['sftpUsername'].setValidators([Validators.required]);
      this.processCodeForm.controls['sftpPassword'].setValidators([Validators.required]);
      this.processCodeForm.controls['sftpIpaddress'].setValidators([Validators.required]);
      this.processCodeForm.controls['sftpPort'].setValidators([Validators.required]);
      this.processCodeForm.controls['sftpPath'].setValidators([Validators.required]);
    }
    else {
      this.showsftpValues = false;
      this.processCodeForm.controls.sftpRequired.setValue(this.sftpValues);
      this.processCodeForm.controls['sftpUsername'].reset();
      this.processCodeForm.controls['sftpPassword'].reset();
      this.processCodeForm.controls['sftpIpaddress'].reset();
      this.processCodeForm.controls['sftpPort'].reset();
      this.processCodeForm.controls['sftpPath'].reset();
      this.processCodeForm.controls['sftpUsername'].clearValidators();
      this.processCodeForm.controls['sftpPassword'].clearValidators();
      this.processCodeForm.controls['sftpIpaddress'].clearValidators();
      this.processCodeForm.controls['sftpPort'].clearValidators();
      this.processCodeForm.controls['sftpPath'].clearValidators();
    }
    this.processCodeForm.controls['sftpUsername'].updateValueAndValidity();
    this.processCodeForm.controls['sftpPassword'].updateValueAndValidity();
    this.processCodeForm.controls['sftpIpaddress'].updateValueAndValidity();
    this.processCodeForm.controls['sftpPort'].updateValueAndValidity();
    this.processCodeForm.controls['sftpPath'].updateValueAndValidity();
  }
  whiteSpaceValidation(event){
    console.log(`${event.target.name} - ${event.target.value}`);
  }
  finalSubmitofForm(formValue) {
    this.processCodeForm.controls.creatorId.setValue(this.currentUser);
    this.apiService
      .createExtSys(this.processCodeForm.value)
      .subscribe((resp) => {
        this.auditlogData = resp;
        this.auditLog();
        console.log("Audit Data",this.auditlogData, this.auditlogData.creatorId);
        if (this.auditlogData) {
          Swal.fire({
            title: 'Record is Created!',
            icon: 'success'
          });
          this.submit = true;
          this.formTouched=!this.processCodeForm.touched;
          // this.processCodeForm.reset();
        }
        else {
          Swal.fire({
            title: 'Failed to Save Data!',
            icon: 'error'
          });
        }
      },
        error => {
          if (HttpErrorResponse) {
            Swal.fire({
              text: 'Server Error!',
              icon: 'error'
            });
          }
        });

  }
  enableEditFlag() {
    this.submit = false;
    this.showSubmit = true;
  }
  updateSubmitofForm(formValue){
      this.processCodeForm.controls.creatorId.setValue(this.currentUser);
      this.apiService
        .createExtSys(this.processCodeForm.value)
        .subscribe((resp) => {
          this.auditlogData  = resp;
          if (this.auditlogData ) {
            Swal.fire({
              title: 'Record is Updated',
              icon: 'success'
            });
            this.showSubmit = false;
            this.formTouched=!this.processCodeForm.touched;
            // this.processCodeForm.reset();
            this.auditLog();
          }
          else {
            Swal.fire({
              title: 'Failed to Update Data!',
              icon: 'error'
            });
          }
        },
          error => {
            if (HttpErrorResponse) {
              Swal.fire({
                text: 'Server Error!',
                icon: 'error'
              });
            }
          });
  
  
    
  }
  onClickOfAuth() {
    if (this.auditlogData.creatorId == this.currentUser) {
      Swal.fire({
        title: 'Maker Cannot Authorized the Record!',
        icon: 'error'
      });
    } else {
      Swal.fire(
        {
          //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
          text: 'You are trying to Authorize the record. ' + ' Do you want to proceed?',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          // confirmButtonText: 'PROCEED.'
          cancelButtonText: 'NO',
          confirmButtonText: 'YES', 'icon': 'info'
        }
      ).then((result => {
        console.log("this is reopen ", result);
        if (result.isConfirmed === true) {
          let payload:any={}
      
        payload.externalSystemName= this.auditlogData.extSysName;
        payload.loggedInUser=this.currentUser,
        payload.processCode=this.auditlogData.processCode;
      this.extName =  this.auditlogData.extSysName;
      this.apiService.authProcessCodeRecord(payload).subscribe(authResp => {
        this.auditlogData = authResp;
        console.log("Auth",  this.auditlogData );
        Swal.fire({
          title: ' Record is Authorized',
          icon: 'success'
        });
        this.auditLog();
      })
    }}));
    }
  }

  onClickOfClose() {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Close the record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      console.log("this is reopen ", result);
      if (result.isConfirmed === true) {

    this.auditlogData.modifiedBy = this.currentUser;
    this.extName =  this.auditlogData.extSysName;
    let payload:any={}
      
    payload.externalSystemName= this.auditlogData.extSysName;
    payload.loggedInUser=this.currentUser,
    payload.processCode=this.auditlogData.processCode;
    this.apiService.closingProcessCodeRecord(payload).subscribe(closeResp => {
      console.log(closeResp);
      this.auditlogData = closeResp;
      if (this.auditlogData) {
        Swal.fire({
          title: 'Record is Closed',
          icon: 'success'
        });
        this.auditLog();
      }
      else {
        Swal.fire({
          title: 'Failed to Close the Recored!',
          icon: 'error'
        });
      }
    },
      error => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });
    }}))
  }
  onClickOfReopen() {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Reopen the record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      console.log("this is reopen ", result);
      if (result.isConfirmed === true) {
        let payload:any={}
      
        payload.externalSystemName= this.auditlogData.extSysName;
        payload.loggedInUser=this.currentUser,
        payload.processCode=this.auditlogData.processCode;
    this.extName =  this.auditlogData.extSysName;
    this.apiService.reopeningProcessCodeRecord(payload).subscribe(openResp => {
      console.log(openResp);
      this.auditlogData = openResp;
      if (this.auditlogData) {
        Swal.fire({
          title: 'Record is Reopened',
          icon: 'success'
        });
        this.auditLog();
      }
      else {
        Swal.fire({
          title: 'Failed to Reoped the Recored!',
          icon: 'error'
        });
      }
    },
      error => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });
    }}));

  }
  OndeleteofUser() {
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Delete the record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      if (result.isConfirmed === true) {
    this.extName =  this.auditlogData.extSysName;
    this.apiService.deleteProcessRecord(this.extName,this.auditlogData.processCode,this.currentUser).subscribe(resp => {
      console.log(resp);
      if (resp === "extSys has been deleted!") {
        Swal.fire({
          title: 'Record is Deleted',
          icon: 'success'
        });
        this.router.navigateByUrl('/external-system/processCodeMapping');
      } else {
        Swal.fire({
          text: 'Failed to Delete User Data!',
          icon: 'error'
        });
      }
    },error=>{
      console.log(error.error.message)
      if (error.error.message === "extSys has been deleted!") {
        Swal.fire({
          title: 'Record is Deleted',
          icon: 'success'
        });
        this.router.navigateByUrl('/external-system/processCodeMapping');
      } else {
        Swal.fire({
          text: 'Failed to Delete User Data!',
          icon: 'error'
        });
      }
    });
  }}));
  }

  handleInput(event) {
    if (event.target.selectionStart === 0 && event.code === 'Space'){
      event.preventDefault();
    }
}
}
