import { HttpErrorResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
import { Subject } from 'rxjs';
import { permissionsLabels } from 'src/app/shared/models/fmosNewRolePermissions';
import { ApiService } from 'src/app/shared/services/api.service';
import { RoleService } from 'src/app/shared/services/role.service';
import { ToastService } from 'src/app/shared/services/toast.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'npr-process-code-mapping',
  templateUrl: './process-code-mapping.component.html',
  styleUrls: ['./process-code-mapping.component.scss'],
})
export class ProcessCodeMappingComponent implements OnInit {
  processCodeMappingSummry: any;
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject<any>();
  isLoading: boolean = false;
  roleCodes = new permissionsLabels();
  statusArr: String[] = ['Authorized', 'Unauthorized', 'Open', 'Close'];
  currentUser: any;
  processCodeSummary: any;
  extName: any;

  constructor(
    private apiService: ApiService,
    private router: Router,
    private roleService: RoleService,
    private formBuilder: FormBuilder,
    private toastService: ToastService
  ) {}

  ngOnInit(): void {
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength: 5,
      columnDefs: [{ type: 'date', targets: [5] }],
      order: [[5, 'desc']],
      processing: true,
      lengthMenu: [
        [5, 10, 20, 30,-1],
        [5, 10, 20, 30,"ALL"],
      ],
    };
    this.currentUser = localStorage.getItem("userFromLogin");
    this.getprocessCodeMappingSummry();
    this.roleService.fetchScreenPermissions('Process Code Mapping');
    this.roleService.screenLabelList.subscribe(
      (message) => (this.roleCodes = message)
    );
    console.log(this.roleCodes);
  }
  auditLog() {
    if (this.processCodeSummary.authStatus =='U' || this.processCodeSummary.authStatus =='Unauthorized') {
      this.processCodeSummary.authStatus = 'UNAUTHORIZED'
    }
    if (this.processCodeSummary.authStatus == 'A' || this.processCodeSummary.authStatus =='Authorized' ) {
      this.processCodeSummary.authStatus = 'AUTHORIZED'
    }
    if (this.processCodeSummary.recordStatus == 'O') {
      this.processCodeSummary.recordStatus = 'OPEN'
    }
    if (this.processCodeSummary.recordStatus == 'C') {
      this.processCodeSummary.recordStatus = 'CLOSED'
    }
    if (this.processCodeSummary.verifiedOnce == 'N') {
      console.log("NO");
      this.processCodeSummary.verifiedOnce = 'NO'
    }
    if (this.processCodeSummary.verifiedOnce == 'Y') {
      this.processCodeSummary.verifiedOnce = 'YES'
    }
  }
  ngOnDestroy(): void {
    this.dtTrigger.unsubscribe();
  }

  getprocessCodeMappingSummry() {
    this.isLoading = true;
    this.apiService.getAllExtSys().subscribe((data) => {
      for (let item of data.result) {
        item.authStatus = this.statusArr.find((i) =>
          i.startsWith(item.authStatus)
        );
        item.recordStatus = this.statusArr.find((i)=>
        i.startsWith(item.recordStatus)
        );
      }
      this.processCodeMappingSummry = data.result;
      console.log(this.processCodeMappingSummry);
      this.isLoading = false;

      this.dtTrigger.next();
    });
  }

  close(item){
    console.log(item);
    this.processCodeSummary = item;

    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Close the record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      console.log("this is reopen ", result);
      if (result.isConfirmed === true) {

    this.processCodeSummary.modifiedBy = this.currentUser;
    this.extName =  this.processCodeSummary.extSysName;
    let payload:any={}
      
    payload.externalSystemName= this.processCodeSummary.extSysName;
    payload.loggedInUser=this.currentUser,
    payload.processCode=this.processCodeSummary.processCode;
    this.apiService.closingProcessCodeRecord(payload).subscribe(closeResp => {
      console.log(closeResp);
      this.processCodeSummary = closeResp;
      if (this.processCodeSummary) {
        Swal.fire({
          title: 'Record is Closed',
          icon: 'success'
        }).then(() => window.location.reload());
        this.auditLog();
      }
      else {
        Swal.fire({
          title: 'Failed to Close the Recored!',
          icon: 'error'
        });
      }
    },
      error => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });
    }}))

  }
  open(item){
    this.processCodeSummary = item;
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Reopen the record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      console.log("this is reopen ", result);
      if (result.isConfirmed === true) {

    this.extName =  this.processCodeSummary.extSysName;
    let payload:any={}
      
    payload.externalSystemName= this.processCodeSummary.extSysName;
    payload.loggedInUser=this.currentUser,
    payload.processCode=this.processCodeSummary.processCode;
    this.apiService.reopeningProcessCodeRecord(payload).subscribe(openResp => {
      console.log(openResp);
      this.processCodeSummary = openResp;
      if (this.processCodeSummary) {
        Swal.fire({
          title: 'Record is Reopened',
          icon: 'success'
        }).then(() => window.location.reload());
        this.auditLog();
      }
      else {
        Swal.fire({
          title: 'Failed to Reoped the Recored!',
          icon: 'error'
        });
      }
    },
      error => {
        if (HttpErrorResponse) {
          Swal.fire({
            text: 'Server Error!',
            icon: 'error'
          });
        }
      });
    }}));


  }
  delete(item){
    this.processCodeSummary = item;
     Swal.fire(
    {
      //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
      text: 'You are trying to Delete the record. ' + ' Do you want to proceed?',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      // confirmButtonText: 'PROCEED.'
      cancelButtonText: 'NO',
      confirmButtonText: 'YES', 'icon': 'info'
    }
  ).then((result => {
    console.log("this is reopen ", result);
    if (result.isConfirmed === true) {
  this.extName =  this.processCodeSummary.extSysName;
  this.apiService.deleteProcessRecord(this.extName,this.processCodeSummary.processCode,this.currentUser).subscribe(resp => {
    console.log(resp);
    if (resp === "extSys has been deleted!") {
      Swal.fire({
        title: 'Record is Deleted',
        icon: 'success'
      }).then(() => window.location.reload());
      this.router.navigateByUrl('/external-system/processCodeMapping');
    } else {
      Swal.fire({
        text: 'Failed to Delete User Data!',
        icon: 'error'
      });
    }
  },error=>{
    console.log(error.error.message)
  });
}}));
    
  }
  auth(item){
    this.processCodeSummary = item;
    if (this.processCodeSummary.creatorId == this.currentUser) {
      console.log(this.processCodeSummary)
      Swal.fire({
        title: 'Maker Cannot Authorized the Record!',
        icon: 'error'
      });
    } else {
      Swal.fire(
        {
          //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
          text: 'You are trying to Authorize the record. ' + ' Do you want to proceed?',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          // confirmButtonText: 'PROCEED.'
          cancelButtonText: 'NO',
          confirmButtonText: 'YES', 'icon': 'info'
        }
      ).then((result => {
        console.log("this is reopen ", result);
        if (result.isConfirmed === true) {
          let payload:any={}
      
        payload.externalSystemName= this.processCodeSummary.extSysName;
        payload.loggedInUser=this.currentUser,
        payload.processCode=this.processCodeSummary.processCode;
      this.extName =  this.processCodeSummary.extSysName;
      this.apiService.authProcessCodeRecord(payload).subscribe(authResp => {
        this.processCodeSummary = authResp;
        console.log("Auth",  this.processCodeSummary );
        Swal.fire({
          title: ' Record is Authorized',
          icon: 'success'
        }).then(() => window.location.reload());
        this.auditLog();
      })
    }}));
    }

  }
  sendingDataforEdit(element) {
    
    console.log(element);

    if (element) {
      localStorage.setItem('processCodeSummary', JSON.stringify(element));
      this.apiService.setProcessCodeData({
        id: element.id,
        extSysCode: element.extSysCode,
        extSysName: element.extSysName,
        commChannel: element.commChannel,
        creatorDtStamp: element.creatorDtStamp,
        creatorId: element.creatorId,
        destination: element.destination,
        destinationTag: element.destinationTag,
        dtdFile: element.dtdFile,
        errDirectoryName: element.errDirectoryName,
        errQueueName: element.errQueueName,
        errSchemaName: element.errSchemaName,
        errXsltName: element.errXsltName,
        extSysTag: element.extSysTag,
        formatType: element.formatType,
        isDestNode: element.isDestNode,
        isExtSysNode: element.isExtSysNode,
        isModuleNode: element.isModuleNode,
        isProcessNode: element.isProcessNode,
        isServiceNode: element.isServiceNode,
        latestAmendNo: element.latestAmendNo,
        messageType: element.messageType,
        moduleCode: element.moduleCode,
        moduleTag: element.moduleTag,
        msgDuplication: element.msgDuplication,
        processCode: element.processCode,
        processTag: element.processTag,
        recordStatus: element.recordStatus,
        reqDirectoryName: element.reqDirectoryName,
        reqQueueName: element.reqQueueName,
        reqSchemaName: element.reqSchemaName,
        reqXsltName: element.reqXsltName,
        requestTag: element.requestTag,
        respDirectoryName: element.respDirectoryName,
        respQueueName: element.respQueueName,
        respSchemaName: element.respSchemaName,
        respXsltName: element.respXsltName,
        responseTag: element.responseTag,
        serviceCode: element.serviceCode,
        serviceTag: element.serviceTag,
        updatedAt: element.updatedAt,
        verifiedEver: element.verifiedEver,
        verifierDtStamp: element.verifierDtStamp,
        verifierId: element.verifierId,
        xsdFile: element.xsdFile,
        authStatus: element.authStatus,
        updatedBy: element.updatedBy,
        fristTimeAuth: element.fristTimeAuth,
        versionNo: element.versionNo,
        maxUploadLimit: element.maxUploadLimit,
        fileName: element.fileName,
        bdav: element.bdav,
        backDateAllow: element.backDateAllow,
        futureDateAllow: element.futureDateAllow,
        fdav: element.fdav,
        acft: element.acft,
        autoUpload: element.autoUpload,
        filePath: element.filePath,
        fCYRateVariance: element.fCYRateVariance,
        fCYPercentage: element.fCYPercentage,
        sftpUsername: element.sftpUsername,
        sftpPassword: element.sftpPassword,
        sftpPort: element.sftpPort,
        sftpIpaddress: element.sftpIpaddress,
        sftpRequired: element.sftpRequired,
        sftpPath: element.sftpPath,
        mandatoryFieldImposed: element.mandatoryFieldImposed,
        treasuryrefRequired: element.treasuryrefRequired,
        wuFile: element.wuFile,
        verifiedOnce: element.verifiedOnce,
      });
      this.router.navigate(['/external-system/processCodeMappingEdit']);
    }
  }
}
